import React from 'react';
import Sketch from 'react-p5';


let c,y=0;
let speed=3;
let limit=8;

const txt=["Welcome to My Miscellaneous","page.This page consists of",
"my simple p5.js projects","along with some minigames","These are the projects i did in",
"my free time for myself","when i get bored also ","these texts should appear",
"in starwars syles.if it doesn't","then something went wrong","refresh the page ':-)"," or i was to lazy to do that ",
"if it does work may","the force be with you"];

