const setup=(p5,parent)=>{
p5.createCanvas(6.8*(p5.displayWidth/8),p5.displayHeight/2,p5.P3D).parent(parent);
c=p5.join(txt,'\n');
if(p5.width<768){
    speed=5;
    limit=4;
}
y=p5.height;



}
const draw=(p5)=>{
p5.background(0);

p5.fill(238,213,75);
p5.textSize(p5.width*0.08);
p5.textAlign(p5.CENTER);
// p5.rotateX(p5.PI/4);

p5.text(c,0,y,p5.width,p5.height*10);
y-=speed;
if(y<=-p5.height*limit){
y=p5.height;
}

}

export default function Intro() {
    return (
        <div>
            <center><h2>INTRO</h2></center><br />
            {/* <P5Wrapper sketch={sketch}/> */}
            <Sketch setup={setup} draw={draw}/>
        </div>
    )
}
