import React from 'react'
import Sketch from 'react-p5';
let x=0,y=0,k=0;
let speed=50;
let stroke="green";

const drawpoint=(p5)=>{

    p5.stroke(stroke);
    p5.strokeWeight(3);

  let px = p5.map(x,  -2.182, 2.6558, 0, p5.width-50);
  let py = p5.map(y, 0, 9.9983, p5.height-50, 0);

    p5.point(px,py);
    // console.log(px,py);

}
const nextpoint=(p5)=>{
let nx,ny;
let r=p5.random(1);
if (r < 0.01) {
    ny = 0.16 * y;
    nx = 0;
    stroke="brown";
  } else if (r < 0.86) {
    nx = 0.85 * x + 0.04 * y;
    ny = -0.04 * x + 0.85 * y + 1.6;
    stroke="green";
  } else if (r < 0.93) {
    nx = 0.2 * x + -0.26 * y;
    ny = 0.23 * x + 0.22 * y + 1.6;
    stroke="darkgreen";
  } else {
    nx = -0.15 * x + 0.28 * y;
    ny = 0.26 * x + 0.24 * y + 0.44;
    stroke="darkgreen";
  }
// if (r < 0.01) {
//     nx = -0.15 * x + 0.28 * y;
//     ny = 0.26 * x + 0.24 * y + 0.44;
//     stroke="darkgreen";
//   }
//   else if(r < 0.86){
//     nx = 0.2 * x + -0.26 * y;
//     ny = 0.23 * x + 0.22 * y + 1.6;
//     stroke="darkgreen";

//   }
//   else if(r<0.93){
//     nx = 0.85 * x + 0.04 * y;
//         ny = -0.04 * x + 0.85 * y + 1.6;
//         stroke="green";
   


//   }
//   else{
//     ny = 0.16 * y;
//         nx = 0;

//   }
  x = nx;
  y = ny;
}


const setup=(p5,parent)=>{
    p5.createCanvas(7*(p5.displayWidth/8),7*(p5.displayWidth/8)).parent(parent);
    if(p5.width<=768){
        speed=7;
    }
   
    p5.background(0);    
    
    
    
    }

const draw=(p5)=>{
    if(k<1000){


        for(let i=0;i<speed;i++){

            drawpoint(p5);
            nextpoint(p5);
    
    
        }
        k+=1;

    }
   
    
   




}

export default function Barnsleyfern() {
    return (
        <div>
             <center><h2>Barnsley's Fern</h2></center><br />
            <Sketch setup={setup} draw={draw} />
        </div>
    )
}

