import React from 'react';
import Sketch from 'react-p5';
let s;
let score=0;
let food;
let scale=5;
let winscore=50;
function snake(p5){

    this.x=0;
    this.y=0;
    this.score=0;
    this.dir="left";

    this.total=1;
    this.tail=[];
    this.xspeed=3;
    this.yspeed=0;
    this.width=p5.width;
    this.height=p5.height;

    this.update=function(){
        if(this.total===this.tail.length){
          for(var i=0;i<this.tail.length-1;i++){
            this.tail[i]=this.tail[i+1];
          }

        }
        this.tail[this.total-1]=p5.createVector(this.x,this.y);



     
      this.tail[this.total-1]=p5.createVector(this.x,this.y);


      this.x=p5.constrain(this.x+this.xspeed,0,this.width-20);
      this.y=p5.constrain(this.y+this.yspeed,0,this.height-20);
      


      }
      this.show=function(p5){
        p5.fill(255)
        for(let i=0;i<this.tail.length;i++){
          p5.rect(this.tail[i].x,this.tail[i].y,20,20);
        }
        p5.rect(this.x,this.y,20,20);
      }


      this.generatefood=(p5)=>{
        let flag=0
          food.x=Math.floor(20+Math.random()*(p5.width-30));
          food.y=Math.floor(20+Math.random()*(p5.height-30));
          for (let i=0;i<this.tail.length;i++){
            if(this.tail.x===food.x || this.tail.y===food.y){
              flag=1;
              break;
            }
          }
          if(flag===1){
            this.generatefood(p5);
          }
          else{
            p5.rect(food.x,food.y,20,20)
          }
          
      
      
      
      }
      




}
const eat=(s)=>{

s.total++;


}
const increasescore=(s)=>{
  s.score+=1;
score+=1


}


const setup=(p5,parent)=>{
p5.createCanvas(7*(p5.windowWidth)/8,7*(p5.windowWidth)/16).parent(parent);
if (p5.width<=780){
  scale=2;
  winscore=20;
}
s = new snake(p5)
food= p5.createVector(Math.floor(Math.random()*(p5.width-20)/20),Math.floor(Math.random()*(p5.height-20)/20))

s.generatefood(p5);



}

const draw=(p5)=>{
  if(s.score<winscore){
    p5.background(0);
    s.update();
    // p5.fill(255)
    s.show(p5);
  //  p5.rect(s.x,s.y,20,20);
   p5.fill(255,0,0);
   p5.rect(food.x,food.y,20,20);
   if (p5.dist(s.x,s.y,food.x,food.y)<10){
    for(let c=0;c<scale;c++){
      eat(s);  
    s.update();  
    
    }
    increasescore(s);
    s.generatefood(p5,s);

        
   }
   p5.noStroke(); 
    p5.fill("lightgreen"); 
    // set text size and alignment 
    p5.textSize(20); 
    p5.textAlign(p5.CENTER); 
    // display the distance calculated 
    p5.text("score = "+ p5.str(score)+"\n score "+p5.str(winscore)+" to win", p5.width/2, p5.height-50); 
  



  }
  else{
    p5.background(0);
    p5.noStroke(); 
    p5.fill("lightgreen"); 
    // set text size and alignment 
    p5.textSize(40); 
    p5.textAlign(p5.CENTER); 
    // display the distance calculated 
    p5.text("YOU WIN \n  ", p5.width/2, p5.height/2); 
  

    
  }
   

}
const  keyPressed=(p5) =>{
    if (p5.keyCode === 83) {
      if(s.dir !== "UP"){
        s.xspeed=0;
        s.yspeed=3;
        s.dir="DOWN";
      }
     
    } else if (p5.keyCode === 68) {
      if(s.dir !=="LEFT"){
        s.xspeed=3;
        s.yspeed=0;
        s.dir="RIGHT";
      }
     
    }
    else if (p5.keyCode === 65) {
      if(s.dir !=="RIGHT"){
        s.xspeed=-3;
        s.yspeed=0;
        s.dir="LEFT";

      }
        
      }
      else if (p5.keyCode === 87) {
        if(s.dir !=="DOWN"){
          s.xspeed=0;
          s.yspeed=-3;
          s.dir="UP";

        }
      
      }
  }
  const  mouseClicked=(p5)=> {
   if (p5.mouseX<p5.width/2 && p5.mouseY<=3*p5.height/4 && p5.mouseY>=p5.height/4){
    s.xspeed=-5;
    s.yspeed=0;
   } 
   else if (p5.mouseY>p5.height/2 && p5.mouseX<=3*p5.width/4 && p5.mouseX>=p5.width/4) {
    s.xspeed=0;
    s.yspeed=5;
  } else if (p5.mouseX>p5.width/2 && p5.mouseY<=3*p5.height/4 && p5.mouseY>=p5.height/4) {
    s.xspeed=5;
    s.yspeed=0;
  }
  else if (p5.mouseY<p5.height/2 && p5.mouseX<=3*p5.width/4 && p5.mouseX>=p5.width/4) {
    s.xspeed=0;
     s.yspeed=-5;
   }
   
  }
  

export default function Snake() {
   
    return (
        <div>
           <center><h2>Simple Snake Game</h2></center><br />
            <label>W-S-A-D for direction in pc 
              for mobile tap at the edges (you cannot lose in this game so dont try it)<br /> 
              
              </label>
    

            <Sketch setup={setup} draw={draw} keyPressed={keyPressed} mouseClicked={mouseClicked} />
        </div>
    )
}
